@import url("https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Inter:wght@100;200;300;400;500;600&display=swap");
*{
    font-family: 'Inter', sans-serif;;
}
body{
    width: 100vw;
    height: 100vh;
    background-color: #fc4278;
    /* background-color: #fc4278; */
    display: flex;
    justify-content: center;
    align-items: center;
}
div{
    color: white;
    text-align: center;

}
div h3{
    color: #fff;
    font-size: 20px;
    font-weight: 500;
}
div button{
    border: none;
    color: #fff;
    padding: 10px 20px;
    border-radius: 5px;
    background-color: #182c61;
    font-size: 18px;

}
b{
    color: #3b3b98;
    background-color: #FFFF;
    border-radius: 50%;
    padding: 5px;
}

